let fibonacci = function (){
    let cache = [0, 1];
    return function fib(val){
        let result;
        if(typeof cache[val] === 'number'){
            result = cache[val];
        }else{
            result = fib(val-1) + fib(val-2);
            cache[val] = result;
        }
        return result;
    }
}();

import {test} from 'ava'
test(t=>t.is(fibonacci(5), 5))